import axios from 'axios'
import React from "react";
import Pusher from 'pusher-js';
import Page from '../components/Page';
import strings from '../constants/strings'
import Layout from '../components/Layout.js'
import endpoints from '../constants/endpoints'
import credentials from '../constants/credentials'
import LetterComponent from '../components/LettersComponent'
import ExchangeRequest from '../components/ExchangeRequest'
import ExchangeResponse from '../components/ExchangeResponse'
import WaitingForVerification from '../components/WaitingForVerification'

export default class extends Page {

    constructor(){
        super()
        this.state = {
            userName: "",
            activeUsers : ["Test","Mary"],
            lettersAssigned: [],
            userSelected: '',
            isWaitingForCounterPartyToVerify: false,
            isVerifyingForCounterParty: false,
            letterToGive: ''
        }
        this.exchangeRequest = {}

        this.retriveUserName()
        this.retriveLetters()
        this.retrieveActiveUsers()
    }

    async componentDidMount(){
        this.pusher = new Pusher(credentials.PUSHER_APP_KEY, {
            cluster: credentials.PUSHER_APP_CLUSTER,
            encrypted: true
        });

        this.channel = this.pusher.subscribe(strings.PUSHER_CHANNEL);

        this.channel.bind(strings.PUSHER_NEW_USER_EVENT, (user) => {
            console.log("Received new user from Pusher: "+user)
            var newActiveUsers = this.state.activeUsers
            newActiveUsers.push(user)
            this.setState({
                activeUsers: newActiveUsers
            })
        });

        this.channel.bind(strings.PUSHER_NEW_EXCHANGE_REQUEST_EVENT, (exchangeRequest) => {
            console.log("Received new exchange request: "+JSON.stringify(exchangeRequest))
            if(exchangeRequest.respond_user == this.state.userName){
                console.log("Exchange request is for me.")
                this.exchangeRequest = exchangeRequest
                this.setState({
                    isVerifyingForCounterParty: true
                })
            }
        });

        this.channel.bind(strings.EXCHANGE_COMPLETED_EVENT, (data) => {
            if(data.request_user == this.state.userName){
                console.log("Exchange request has completed: "+JSON.stringify(data))
                console.log("Letters assigned are now "+this.state.lettersAssigned)
                this.setState({
                    isWaitingForCounterPartyToVerify: false
                })
                this.swapLetters(this.state.letterToGive,data.letterToReceive)
            }
        });

    }

    swapLetters = (letterToGive,letterToReceive) => {
        console.log("Giving away letter "+letterToGive)
        console.log("Receiving letter "+letterToReceive)
        var lettersAssigned = this.state.lettersAssigned
        lettersAssigned[lettersAssigned.indexOf(letterToGive)] = letterToReceive
        console.log("Letters assigned has changed from "+this.state.lettersAssigned+" to "+lettersAssigned)
        this.setState({
            lettersAssigned: lettersAssigned
        })
    }

    cancelWaitingForVerification = (r) => {
        this.setState({
            isWaitingForCounterPartyToVerify: false
        })
    }

    onExchangeRequestSubmitSuccess = (letterToGive) => {
        this.setState({
            isWaitingForCounterPartyToVerify: true,
            letterToGive: letterToGive
        })
    }

    onExchangeResponseSubmitSuccess = (letterToGive,letterToReceive, requestUser) => {
        this.swapLetters(letterToGive,letterToReceive)
        this.setState({
            isVerifyingForCounterParty: false
        })
    }

    onClick = (e) => {
        console.log("Setting user selected to "+e.target.id)
        this.setState({
            userSelected: this.state.activeUsers[e.target.id]
        })
    }

    retriveLetters = () => {
        axios({
            method: 'post',
            url: endpoints.API_GET_ASSIGNED_LETTERS
        })
        .then((response) => {
            console.log('Got letters assigned as '+response.data)
            this.setState({
                lettersAssigned : response.data
            })
        })
        .catch(function (response) {
            //handle error
            console.log(response);
        });
    }

    retrieveActiveUsers = () => {
        axios({
            method: 'post',
            url: endpoints.API_GET_ALL_ACTIVE_USERS
        })
        .then((response) => {
            const activeUsers = response.data.map(session => session.user.name)
            console.log('Parsed active users as '+activeUsers)
            this.setState({
                activeUsers : activeUsers
            })
        })
        .catch(function (response) {
            //handle error
            console.log(response);
        });
    }

    retriveUserName = () => {
        axios({
            method: 'post',
            url: endpoints.API_GET_SESSION
        })
        .then((response) => {
            if (typeof response.data.user === 'undefined')
                return
            console.log('Received username as  '+response.data.user.name)
            this.setState({
                userName : response.data.user.name
            })
        })
        .catch(function (response) {
            //handle error
            console.log(response);
        });
    }

    onLetterSelected = () => {
        console.log("Letters assigned are now "+this.state.lettersAssigned)
    }

    render(){
        console.log("passing in user name into props "+this.state.userName)
        if (this.state.isWaitingForCounterPartyToVerify)
            return(
                <Layout>
                    <WaitingForVerification cancel={this.cancelWaitingForVerification}/>
                </Layout>
            )
        else if(this.state.isVerifyingForCounterParty)
            return(
                <Layout>
                    <div className="form-group">
                        <label>{this.exchangeRequest.request_user} has requested to connect.Please fill in his details below.</label>
                    </div>
                    <ExchangeResponse targetUser={this.exchangeRequest.request_user} userName={this.exchangeRequest.request_user} onExchangeResponseSubmitSuccess={this.onExchangeResponseSubmitSuccess} lettersAvailable={this.state.lettersAssigned} exchangeRequest={this.exchangeRequest}/>
                </Layout>
            )
        else
            return(
                <Layout>
                    <div id="wrapper">
                        <div id="sidebar-wrapper">
                            <nav id="spy">
                                <ul className="sidebar-nav nav">
                                    {this.state.activeUsers.map((user,index) => {
                                        return <li id={index} className="sidebar-brand" onClick={this.onClick}>{user}</li>
                                    })}
                                </ul>
                            </nav>
                        </div>
                        <div id="page-content-wrapper">
                            <div className="page-content inset" data-spy="scroll" data-target="#spy">
                                <LetterComponent onLetterSelected={this.onLetterSelected} userName={this.state.userName} lettersAssigned={this.state.lettersAssigned}/>
                                <div style={{marginTop:'50px',marginBottom:'50px'}} className="form-group">
                                    <label>Fill in the details of the player you would like to exchange with.</label>
                                </div>
                                <ExchangeRequest targetUser={this.state.userSelected} userName={this.state.userName} onExchangeRequestSubmitSuccess={this.onExchangeRequestSubmitSuccess} userSelected={this.state.userSelected} lettersAvailable={this.state.lettersAssigned}/>
                            </div>
                        </div>
                    </div>
                    <style jsx>{`
                      #wrapper {
                      padding-left: 250px;
                      transition: all 0.4s ease 0s;
                    }

                    #sidebar-wrapper {
                      margin-left: -250px;
                      left: 250px;
                      width: 250px;
                      background: #000;
                      position: fixed;
                      height: 100%;
                      overflow-y: auto;
                      z-index: 1000;
                      transition: all 0.4s ease 0s;
                      color: white;
                    }

                    #wrapper.active {
                      padding-left: 0;
                    }

                    #wrapper.active #sidebar-wrapper {
                      left: 0;
                    }

                    #page-content-wrapper {
                      width: 100%;
                    }

                    .sidebar-nav {
                      position: absolute;
                      top: 0;
                      width: 250px;
                      list-style: none;
                      margin: 0;
                      padding: 0;
                    }

                    .sidebar-nav li {
                      line-height: 40px;
                      text-indent: 20px;
                    }

                    .sidebar-nav li a {
                      color: #999999;
                      display: block;
                      text-decoration: none;
                      padding-left: 60px;
                    }

                    .sidebar-nav li a span:before {
                      position: absolute;
                      left: 0;
                      color: #41484c;
                      text-align: center;
                      width: 20px;
                      line-height: 18px;
                    }

                    .sidebar-nav li a:hover,
                    .sidebar-nav li.active {
                      color: #fff;
                      background: rgba(255,255,255,0.2);
                      text-decoration: none;
                    }

                    .sidebar-nav li a:active,
                    .sidebar-nav li a:focus {
                      text-decoration: none;
                    }

                    .sidebar-nav > .sidebar-brand {
                      height: 65px;
                      line-height: 60px;
                      font-size: 18px;
                    }

                    .sidebar-nav > .sidebar-brand a {
                      color: #999999;
                    }

                    .sidebar-nav > .sidebar-brand a:hover {
                      color: #fff;
                      background: none;
                    }

                    .inset {
                      padding: 20px;
                    }

                    @media (max-width:767px) {

                    #wrapper {
                      padding-left: 0;
                    }

                    #sidebar-wrapper {
                      left: 0;
                    }

                    #wrapper.active {
                      position: relative;
                      left: 250px;
                    }

                    #wrapper.active #sidebar-wrapper {
                      left: 250px;
                      width: 250px;
                      transition: all 0.4s ease 0s;
                    }

                    #menu-toggle {
                      display: inline-block;
                    }

                    .inset {
                      padding: 15px;
                    }

                    }
                    `}</style>
                </Layout>
            )
    }

}


